import { Controller, Commands, Block } from '@ali/4ever-cangjie';
import { mergeListData } from '../utils';

export default (editor: Controller, key, isChecked) => {
  const { value } = editor;
  const { document } = value;
  const node = document.getNode(key) as Block;
  const { data } = node;
  if (!data.list) return editor;

  return editor.command(
    Commands.setNodeByKey,
    key,
    mergeListData(data, {
      isChecked,
    }),
  );
};
